import "./Contactus.scss";
import { Row, Col} from 'react-bootstrap';

let Contactus = () => {
  return (
    <div className="aboutus-content">
        <Row className="pb-3">
          <Col sm={12} className="pb-2">
            <section className="bg-img-contactus rounded-2">
              <div className="card-overlay vertical-content-center rounded-2"><h1>Feel Free to Contact Us</h1></div>
            </section>
          </Col>
          <Col sm={12} className="pb-2">
            <div className="info-sec bg-white motto rounded-2">
              <div> <h5>Contact</h5></div>
              <div className="pb-5"><b>95155 88746</b></div>

              <div> <h5>Mail</h5></div>
              <div className="pb-5"><b>info@incredit.in</b></div>

              <div> <h5>Address</h5></div>
              <div><b>4-23-14/2, SLSK House, Shankaramattam Line,</b></div>
              <div><b>Near Shankaramattam Temple, Lalitha Nagar,</b></div>
              <div><b>Visakhapatnam, 530016.</b></div>
            </div>
          </Col>
        </Row>
    </div>
  );
};

export default Contactus;
